import logo from './logo.svg';
import keyIcon from './icons/key.svg';
import mergeIcon from './icons/crafting.svg';
import React  from 'react';
import './App.css';

/*
2 Modes:
1.
*/

class SelectModeButton extends React.Component {
  render(){
    return (<div className="selectable-inset" onClick={this.props.onClick}>
      <img src={this.props.iconInfo.icon} className={this.props.iconInfo.iconClass} fill="#FF00FF"/>
      <p className="select-button-label">{this.props.label}</p>
    </div>);
  }
}

class App extends React.Component {
  constructor(props){
    super(props);
    this.state={mode:"selecting"};
  }

  setGlobalMode(newState){
    this.setState({mode:newState});
  }


  render(){
    switch (this.state.mode) {
      case "unlocking":
      case "augmenting":
      case "selecting":
      default:
        return (<div className="App">
          <div className="ModeButtons">
            <SelectModeButton iconInfo={{icon:keyIcon,   iconClass:"key-logo"}} label="Unlock Content" onClick={() => this.setState({mode:"unlocking"})}/>
            <SelectModeButton iconInfo={{icon:mergeIcon, iconClass:"craft-logo"}} label="Patch LCP" onClick={() => this.setState({mode:"augmenting"})}/>
          </div>
          <p>{this.state.mode}</p>
        </div>);
    }
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
        </header>
      </div>
    );
  }
}

export default App;
